import Vue from 'vue'
import { requestNickname } from '@/api/API'
import Header from '../../components/Header'
import { Field } from 'mint-ui'
Vue.component(Field.name, Field)
export default {
  components: {
    Header
  },
  data () {
    return {
      nick_name: ''
    }
  },
  mounted () {
    let user = this.$store.state.User.user || {}
    this.nick_name = user.nick_name
  },
  methods: {
    async setNickname () {
      let res = await requestNickname(this.nick_name, this.$store.state.User.token)
      window.EventBus.$emit('globalMessage', res.msg)
      if (res.code === 200) {
        let user = this.$store.state.User.user || {}
        user.nick_name = this.nick_name
        this.$store.commit('User/updateUser', user)
        this.$router.go(-1)
      }
    }
  }
}



